import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import { makeStyles } from '@material-ui/core/styles';
import {SearchTabTasksLabel, SearchTabTasksPanel} from './SearchTabTasks';
import {SearchTabFilesLabel, SearchTabFilesPanel} from './SearchTabFiles';
import {SearchTabCredentialsLabel, SearchTabCredentialsPanel} from './SearchTabCredentials';
import {SearchTabKeylogsLabel, SearchTabKeylogsPanel} from './SearchTabKeylog';
import {SearchTabTokensLabel, SearchTabTokensPanel} from './SearchTabTokens';
import {SearchTabCallbacksLabel, SearchTabCallbacksPanel} from './SearchTabCallbacks';
import {SearchTabArtifactsLabel, SearchTabArtifactsPanel} from './SearchTabArtifacts';
import {SearchTabSocksLabel, SearchTabSocksPanel} from './SearchTabSocks';

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
  },
}));

export function Search(props){
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
      setValue(newValue);
  };
  const tabTypes = ["callbacks", "tasks", "files", "credentials", "keylogs", "tokens", "artifacts", "socks"];
  const [queryParams, setQueryParams] = React.useState(new URLSearchParams(""));
  React.useEffect( () => {
    let params = new URLSearchParams(window.location.search);
    // should be "?tab=files&search1=search1&search2=search2&location=location"
    if(params.has("tab")){
        let tabIndex = tabTypes.findIndex( t => t === params.get("tab"));
        if(tabIndex >= 0){
          setQueryParams(params);
        }else{
          console.log("bad tab value")
        }
    }else{
        
    }     
}, [window.location.search]);
React.useEffect( () => {
  let params = new URLSearchParams(window.location.search);
  // should be "?tab=files&search1=search1&search2=search2&location=location"
  if(params.has("tab")){
      let tabIndex = tabTypes.findIndex( t => t === params.get("tab"));
      if(tabIndex >= 0){
        setQueryParams(params);
        setValue(tabIndex);
      }else{
        console.log("bad tab value")
      }
  }else{
      
  }     
}, []);
React.useEffect( () => {
  let currentParams = new URLSearchParams(window.location.search);
  currentParams.set("tab", tabTypes[value]);
  if (window.history.replaceState) {
    //prevents browser from storing history with each change:
    window.history.replaceState(null, "", "/new/search?" + currentParams);
 }
  //window.location.search = currentParams;
}, [value])
const changeSearchParam = (name, value) => {
  let currentParams = new URLSearchParams(window.location.search);
  currentParams.set(name, value);
  if (window.history.replaceState) {
    //prevents browser from storing history with each change:
    window.history.replaceState(null, "", "/new/search?" + currentParams);
 }
}
return (
  <div className={classes.root} style={{height: "100%"}}>
      <AppBar position="static" color="default">
        <Tabs 
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
        >
        {
            tabTypes.map( (tab, index) => {
              switch (tab){
                case "tasks":
                  return <SearchTabTasksLabel key={"taskstab"} />;
                case "files":
                  return <SearchTabFilesLabel key={"filestab"} />;
                case "credentials":
                  return <SearchTabCredentialsLabel key={"credentialstab"} />;
                case "keylogs":
                  return <SearchTabKeylogsLabel key={"keylogstab"} />;
                case "tokens":
                  return <SearchTabTokensLabel key={"tokenstab"} />;
                case "callbacks":
                  return <SearchTabCallbacksLabel key={"callbackstab"} />;
                case "artifacts":
                  return <SearchTabArtifactsLabel key={"artifactstab"} />;
                case "socks":
                  return <SearchTabSocksLabel kye={"sockstab"} />;
                default:
                  return (null);
              }
            })
        }
        </Tabs>
      </AppBar>
      {
        tabTypes.map( (tab, index) => {
          switch(tab){
              case "tasks":
                return <SearchTabTasksPanel key={"taskspanel"} value={value} index={index} queryParams={queryParams} changeSearchParam={changeSearchParam}/>
              case "callbacks":
                return <SearchTabCallbacksPanel key={"callbackspanel"} value={value} index={index} queryParams={queryParams} changeSearchParam={changeSearchParam}/>
              case "files":
                return <SearchTabFilesPanel key={"filespanel"} value={value} index={index} queryParams={queryParams} changeSearchParam={changeSearchParam}/>
              case "credentials":
                return <SearchTabCredentialsPanel key={"credentialspanel"} value={value} index={index} queryParams={queryParams} changeSearchParam={changeSearchParam}/>
              case "keylogs":
                return <SearchTabKeylogsPanel key={"keylogspanel"} value={value} index={index} queryParams={queryParams} changeSearchParam={changeSearchParam}/>
              case "tokens":
                return <SearchTabTokensPanel key={"tokenspanel"} value={value} index={index} queryParams={queryParams} changeSearchParam={changeSearchParam}/>
              case "artifacts":
                return <SearchTabArtifactsPanel key={"artifactspanel"} value={value} index={index} queryParams={queryParams} changeSearchParam={changeSearchParam}/>
              case "socks":
                return <SearchTabSocksPanel key={"sockspanel"} value={value} index={index} queryParams={queryParams} changeSearchParam={changeSearchParam}/>
              default:
                return (null);
          }
        })
      }
  </div>
)
} 
